@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*:hover,
*:focus,
*:active
 {
  outline: none;
  box-shadow: none !important;
  -webkit-appearance: none;
  box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
  border: rgba(255, 255, 255, 0);
}

.create_input::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #7F00FF;
    opacity: 0.6;
}

.create_input{
    text-align: left;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom: 3px solid #7F00FF;
    border-left-color: transparent;
    box-shadow: none;
    outline: 0;
    font-size: 20px;
    font-family: Almarai, sans-serif;
    border-radius: 0%;
}

.create_input:focus{
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    border: rgba(255, 255, 255, 0);  
    border-bottom: 3px solid #7F00FF;
}

.create_btn{
    color:#fff;
    background: #7F00FF;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #E100FF, #7F00FF);
    margin-top: 10px;
}

.create_btn:focus,.create_btn:hover{
    color: #fff;
}
.completeTask{
    text-decoration: line-through;
}

.table th{
    padding-top: 10px;
    color: #7F00FF;
}

.th1{
    max-width: 100px;
    min-width: 100px;
}

.table{
    width: 100%;
}

.list_row{
    max-width: 220px;
    min-width: 220px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.crudBtn{
    font-size:10px;
}
body{
    background: #7F00FF;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #E100FF, #7F00FF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.todo-header{
    font-family: 'Lobster', cursive;
    background:linear-gradient(to right, #ece9e6, #ffffff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 4rem;
    text-align:center;
    padding-top: 20px;
}

.form_container{
    background:#fff;
    width:500px;
    height:500px;
    border-radius:30px;
    padding:10px;
    margin:0 auto;
    overflow-y:auto;
    box-shadow: 0px 0px 12px rgb(95, 93, 93);
    margin-top:40px;
}

::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
    background: #7F00FF;
}
